import { Box, MenuItem, Stack, TextField, Typography } from "@mui/material";
import { useState } from "react";


export const MultiSelect = () =>{

    const data = [{key:"Taiwan",value:"tw"},{key:"USA",value:"us"},{key:"HongKong",value:"hk"},{key:"Japan",value:"jp"}]

    const [country,setCountry] = useState('');
    const [countries,setCountries] = useState([]);
    console.log({country});
    console.log({countries});
    const handleChangeCity = (e)=>{
        setCountry(e.target.value);
    }
    const handleChangeCities = (e)=>{
        setCountries(e.target.value);
    }

    return (
        <Stack width="250px" spacing={2}>
            <TextField label="Select country" select value={country} onChange={handleChangeCity} fullWidth helperText="Please choose your countries."> 
                <MenuItem value="tw">Taiwan</MenuItem>
                <MenuItem value="us">USA</MenuItem>
                <MenuItem value="hk">HongKong</MenuItem>
                <MenuItem value="jp">Japan</MenuItem>
            </TextField>

            <TextField label="Select countries" select value={countries} onChange={handleChangeCities} fullWidth 
                SelectProps={
                    {multiple:true}
                }
                size="small"
                color="success"
                helperText="Please choose your countries."
            > 
                {data.map((country)=>{return (<MenuItem value={country.value}>{country.key}</MenuItem>)})}
            </TextField>

            <Typography variant="subtitle1" color="primary">Your choice: {country}</Typography>
            <Typography variant="subtitle1" color="success">Your choices: {countries.join(",")}</Typography>
        </Stack>
    );
};



