import ButtonGroup from '@mui/material/ButtonGroup'
import Button from '@mui/material/Button'

import { Stack } from '@mui/material';
import { Outlet,Link } from 'react-router-dom';

export const Layout = ()=>{
    return (
        <Stack spacing={4}>
            <Stack spacing={2} direction="row">
                <ButtonGroup variant="text" spacing={2}>
                    <Button component={Link} to="/MUI">Typography</Button>
                    <Button component={Link} to="button">Button</Button>
                    <Button component={Link} to="textfield">TextField</Button>
                    <Button component={Link} to="select">Select</Button>
                </ButtonGroup>
            </Stack>
            <hr />
            <Outlet/>
        </Stack>
        
    );
}