import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import { MultiButton } from './components/MultiButton';
import { MultiTextField } from './components/MultiTextField';
import { MultiTypography } from './components/MultiTypography';
import { Layout } from './Layout';
import { MultiSelect } from './components/MultiSelect';


function App() {
  return (
    <div className="App">

      <BrowserRouter>
        <Routes>
          <Route path="/MUI" element={<Layout />}>
            <Route index element={<MultiTypography />}></Route>
            <Route path="button" element={<MultiButton />}></Route>
            <Route path="textfield" element={<MultiTextField />}></Route>
            <Route path="select" element={<MultiSelect />}></Route>
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
