import { InputAdornment, Stack, TextField, IconButton } from "@mui/material";
import { useState } from "react";
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

export const MultiTextField = ()=>{
    const [name,SetName] = useState('');
    const [show,SetShow] = useState(true);

    return (
        <Stack spacing={4}>
            <Stack spacing={2} direction="row">
                <TextField label="姓名" variant="filled" />
                <TextField label="姓名" variant="outlined" />
                <TextField label="姓名" variant="standard" />
            </Stack>

            <Stack spacing={2} direction="row">
                <TextField label="Small" size="small" color="error" />
                <TextField label="Medium" size="medium" color="primary" />
            </Stack>

            <Stack spacing={2} direction="row">
                <TextField label="姓名" required error />
                <TextField label="密碼" type="password" helperText="Do not show your dick!" disabled />
                <TextField label="住址" value="高雄市苓雅區河北路79號" InputProps={{ readOnly:true}} />
            </Stack>

            
            <Stack spacing={2} direction="row">
                <TextField label="金額" InputProps={
                    {startAdornment: <InputAdornment position="start">$</InputAdornment>}
                } />
                <TextField label="體重" InputProps={
                    {endAdornment: <InputAdornment position="end">kg</InputAdornment>}
                } />
            </Stack>

            <Stack spacing={2} direction="row">
                <TextField label="姓名" value={name} onChange={(e)=>{SetName(e.target.value)}} required error={!name} helperText={!name? '請輸入姓名':''} />
                <TextField label="密碼" type={show? 'text':'password'} InputProps={
                    {endAdornment: <InputAdornment position="end">
                        <IconButton onClick={()=>SetShow(!show)}>
                          {show? <Visibility/>:<VisibilityOff/>}
                        </IconButton>
                    </InputAdornment>}
                } />
            </Stack>
        </Stack>
    )
};